import React, { Component } from 'react';
import './App.css';
import AppBar from 'material-ui/AppBar';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import Drawer from 'material-ui/Drawer';
import MenuItem from 'material-ui/MenuItem';
import NavigationClose from 'material-ui/svg-icons/navigation/close';
import IconButton from 'material-ui/IconButton';
import './litera.min.css';
import Jumbo from './jumbo.js';
import GC from './Card.js';
import Divider from 'material-ui/Divider';
import Footer from './Footer.js';
import Application from './applic.js';
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {open: false};
  }

handleToggle = () => this.setState({open: !this.state.open});
 handleClose = () => this.setState({open: false});

  render() {
    return (
      <div className="App">
        <MuiThemeProvider>
          
          <AppBar
             title="Visista"
             iconClassNameRight="muidocs-icon-navigation-expand-more" onClick={this.handleToggle}
             onRequestChange={(open) => this.setState({open: false})}
             style={{ backgroundColor: '#DC143C',position: 'fixed',top: 0+'px', }}
          />
         <Drawer open={this.state.open}  width={250}>
          <IconButton><NavigationClose onClick={this.handleClose}/></IconButton>
             <MenuItem onClick={this.handleClose}>Supervised   Learning</MenuItem>
              <MenuItem onClick={this.handleClose}>Unsupervised Learning</MenuItem>
              <MenuItem onClick={this.handleClose}>Reinforcement Learning</MenuItem>
              <Divider/>
              <h5>Industries</h5>
         </Drawer>
         <Jumbo ></Jumbo>
         <h2>UNDERSTANDING</h2>
         <h3 style={{textAlign: 'left',paddingLeft: 20+'px',paddingRight: 470+'px',display: 'inline'}}>Supervised</h3>
         <a style={{textAlign: 'right',paddingLeft: 470+'px',display: 'inline'}}>See all</a>
         <GC></GC>
         <br/><br/>
         <h3 style={{textAlign: 'left',paddingLeft: 20+'px',paddingRight: 470+'px',display: 'inline'}}>Unsupervised</h3>
         <a style={{textAlign: 'right',paddingLeft: 470+'px',display: 'inline'}}>See all</a>
         <GC></GC>
         <br/><br/>
         <h3 style={{textAlign: 'left',paddingLeft: 20+'px',paddingRight: 470+'px',display: 'inline'}}>Reinforcement</h3>
         <a style={{textAlign: 'right',paddingLeft: 470+'px',display: 'inline'}}>See all</a>
         <GC></GC>
         <br/><br/>
         <Application></Application>
        </MuiThemeProvider> 
      <Footer/>
      </div>
    );
  }
}

export default App;



