import React, { Component } from 'react';
import RaisedButton from 'material-ui/RaisedButton';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';

const style = {
  margin: 12,
};


class Footer extends Component {
  render() {
    return (
      <div style={{backgroundColor: '#302828'}}>

  <footer class="text-center" style={{color: 'white'}}>
        <div class="footer-above">
            <div class="container" >
                <div class="row">
                    <div class="footer-col col-md-4">
                        <h3>Location</h3>
                        <p style={{paddingTop: 27+'px'}}>701-C, Modi Splendour <br/>
                        
                        Mettukaniguda,Hyderabad-500055. </p>
                    </div>
                    <div class="footer-col col-md-4">
                        <h4>Around The Web</h4>
                        <ul class="list-inline" style={{paddingTop: 27+'px'}}>
                            
                            <li style={{display: 'inline',paddingRight: 15+'px',}}>
                                <a href="www.facebook.com/pbpranavk" class="btn-social btn-outline"><i class="fa fa-facebook-official fa-4x" ></i></a>
                            </li>
                            <li style={{display: 'inline'}}>
                                <a href="www.twitter.com/pbpranavk" class="btn-social btn-outline"><i class="fa fa-fw fa-twitter fa-4x"></i></a>
                            </li>
                            <li style={{display: 'inline'}}>
                                <a href="www.github.com/pbpranavk" class="btn-social btn-outline"><i class="fa fa-fw fa-github fa-4x"></i></a>
                            </li>
		                    
                        </ul>
                    </div>
                    <div class="footer-col col-md-4">
                        
                        <h5>Become a sponsor</h5>
                         <div style={{paddingLeft: 128+'px'}} class="col-lg-8 col-lg-offset-2 text-center">
                    <MuiThemeProvider>     
                    <a style={{paddingTop: 18+'px'}} href="https://www.patreon.com/pbpranavk" class="btn btn-lg btn-outline">
                    <RaisedButton label="Patreon" style={style} />
                    </a>
                    </MuiThemeProvider>
                </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="footer-below"style={{backgroundColor: 'black'}}>
            <div class="container" >
                <div class="row">
                    <div class="col-lg-12">
                        Copyright &copy; pbpranavk 2017
                    </div>
                </div>
            </div>
        </div>
    </footer>

    
    <div class="scroll-top page-scroll visible-xs visible-sm">
        
    </div>

    </div>

        );
}}

export default Footer;


